.card {
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.136);
  backdrop-filter: blur(5px);
  box-shadow: 0 20px 50px rgba(211, 211, 211, 0.15);
  border-top: 1px solid rgba(186, 186, 186, 0.5);
  border-left: 1px solid rgba(194, 194, 194, 0.5);
}
.table {
  color: white;
  text-align: center;
}

.creator {
  text-align: center;
}

.rl {
  margin-bottom: 5px;
  text-align: center;
}

form {
  margin-left: auto;
  margin-right: auto;
}

.nc {
  margin-top: auto;
  margin-bottom: auto;
  border: 1px solid rgb(85, 85, 85);
  border-radius: 20px;
}

.form-control {
  margin-right: 10px;
  border-radius: 20px;
  width: 300px;
  background-color: rgb(64, 64, 64);
  border-color: rgb(56, 56, 56);
}

.btn {
  border-radius: 200px;
  margin-left: 10px;
  color: white;
}
.btn:hover {
  color: black;
  background-color: white;
}

@media (min-width: 100px) and (max-width: 400px) {
  .card {
    width: auto;
    margin-top: 20px;
    margin-bottom: -20px;
  }
  .table{
    margin-bottom: 0px;
  }
  .form-control {
    width: auto;
  }
  #d-flex {
    display: block !important;
  }
}
